<template>
	<div id="offcanvas-nav" uk-offcanvas="overlay: true; flip: true" class="uk-height-viewport">
		<div class="uk-offcanvas-bar">
			<Logo />
			<ul class="uk-nav uk-nav-default uk-margin-medium-top">
				<li class="uk-nav-header">Men√∫</li>
				<li class="uk-nav-divider"></li>
				<li>
					<router-link to="/" class="uk-offcanvas-close">
						<span class="uk-margin-small-right" uk-icon="icon: logo"></span> {{'HOME' | translate}}
					</router-link>
				</li>
				<li>
					<router-link to="/series" class="uk-offcanvas-close">
						<span class="uk-margin-small-right" uk-icon="icon: serie"></span> {{'SERIES' | translate}}
					</router-link>
				</li>
				<li>
					<router-link to="/authors" class="uk-offcanvas-close">
						<span class="uk-margin-small-right" uk-icon="icon: author"></span> {{'AUTHOR_ALL' | translate}}
					</router-link>
				</li>
				<li class="uk-nav-header">Mi Cuenta</li>
				<li class="uk-nav-divider"></li>

				<span v-if="username">
					<li>
						<router-link :to="{ name: 'profile' }" class="uk-offcanvas-close">
							<span class="uk-margin-small-right" uk-icon="icon: profile"></span> {{'PROFILE' | translate}}
						</router-link>
					</li>
					<li>
						<router-link :to="{ name: 'profile' }" class="uk-offcanvas-close">
							<span class="uk-margin-small-right" uk-icon="icon: follow"></span> {{'FAVORITES' | translate}}
						</router-link>
					</li>
					<li>
						<a @click="logout"><span class="uk-margin-small-right" uk-icon="icon: sign-out"></span> {{'LOGOUT' | translate}}</a>
					</li>
				</span>
				
				<li v-else>
					<a href="#modal-login" uk-toggle>
						<span class="uk-margin-small-right" uk-icon="icon: sign-in"></span> {{'LOGIN_TITLE' | translate}}
					</a>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import Logo from "./Logo.vue";

	export default {
		name: "Offcanvas",
		mounted: function () {
			this.loadUsername();
		},
		components: {
			Logo
		},
		methods: {
			logout() {
				this.$store.dispatch("auth/logout");
				this.loadUsername();
				this.$router.go();
			},
			loadUsername() {
				setTimeout(() => {
					this.username = this.$store.getters["auth/getUsername"];
					this.userId = this.$store.getters["auth/getId"];
				}, 1000);
			},
		},
		data() {
			return {
				username: null,
				userId: null,
			};
		}
	};
</script>