import './assets/css/style.scss'
import Vue from "vue"
import VueApollo from "vue-apollo"
import VueRouter from "vue-router"
import apolloClient from "./vue-apollo"
import VueSocialSharing from 'vue-social-sharing'
import App from "./App.vue"
import store from './../store/index'
import VueMeta from 'vue-meta'
//import VueGtag from "vue-gtag"
import CreateGtm from 'vue-gtm';

/* lang filters */
import './filters/translate.filter';

/* atributes on head like title */
import vueHeadful from 'vue-headful';

/* PWA app */
//import './registerServiceWorker'

Vue.use(VueApollo);
Vue.use(VueRouter);
Vue.config.productionTip = false;
Vue.use(VueSocialSharing);
Vue.component('vue-headful', vueHeadful);
Vue.use(VueMeta);
/*Vue.use(VueGtag, {
	config: { id: "G-KJH51NNHZP" },
	appName: 'Ink&Tales',
	pageTrackerScreenviewEnabled: true
}, router);*/

const apolloProvider = new VueApollo({
	defaultClient: apolloClient
});

Vue.use(CreateGtm, {
	id: 'GTM-KR45KS9',
	defer: false,
	enabled: true,
	debug: true,
	loadScript: true,
	vueRouter: router,
	trackOnNextTick: false,
});

const router = new VueRouter({
	mode: "history",
	routes: [
	{
		path: "/",
		name: 'home',
		components: require("./containers/Home.vue")
	},
	{
		path: "/about",
		name: 'about',
		component: () => import(/*webpackChunkName: "about" */ "./containers/About.vue")
	},
	{
		path: "/help",
		name: 'help',
		component: () => import(/*webpackChunkName: "help" */ "./containers/Help.vue")
	},
	{
		path: "/publish",
		name: 'publish',
		component: () => import(/*webpackChunkName: "publish" */ "./containers/Publish.vue")
	},
	{
		path: "/terms",
		name: 'terms',
		component: () => import(/*webpackChunkName: "terms" */ "./containers/Terms.vue")
	},
	{
		path: "/privacy",
		name: 'privacy',
		component: () => import(/*webpackChunkName: "privacy" */ "./containers/Privacy.vue")
	},
	{
		path: "/content",
		name: 'content',
		component: () => import(/*webpackChunkName: "content" */ "./containers/Content.vue")
	},
	{
		path: "/shop",
		name: 'shop',
		component: () => import(/*webpackChunkName: "shop" */ "./containers/Shop.vue")
	},
	{
		path: "/redirigiendo",
		name: 'redirigiendo',
		components: require("./containers/Redirigiendo.vue")
	},
	{
		path: "/gracias",
		name: 'gracias',
		component: () => import(/*webpackChunkName: "gracias" */ "./containers/Gracias.vue")
	},
	{
		path: "/error",
		name: 'error',
		component: () => import(/*webpackChunkName: "error" */ "./containers/Error.vue")
	},
	{
		path: "/pendiente",
		name: 'pendiente',
		component: () => import(/*webpackChunkName: "pendiente" */ "./containers/Pendiente.vue")
	},
	{
		path: "/series/",
		name: 'series',
		component: () => import(/*webpackChunkName: "series" */ "./containers/Series.vue")
	},
	{
		path: "/serie/:id",
		name: 'serie',
		component: () => import(/*webpackChunkName: "serie" */ "./containers/Serie.vue")
	},
	{
		path: "/serie/:serie/chapter/:chapter",
		components: require("./containers/Chapter.vue")
	},
	{
		path: "/category/:id",
		components: require("./containers/Category.vue")
	},
	{
		path: "/genres/",
		name: 'genres',
		component: () => import(/*webpackChunkName: "genres" */ "./containers/Genres.vue")
	},
	{
		path: "/genre/:id",
		components: require("./containers/Genre.vue")
	},
	{
		path: "/author/:id",
		components: require("./containers/Author.vue")
	},
	{
		path: "/authors/",
		name: 'authors',
		component: () => import(/*webpackChunkName: "authors" */ "./containers/Authors.vue")
	},
	{
		path: "/viewer/:serie/:chapter",
		name: 'viewer',
		component: () => import(/*webpackChunkName: "viewer" */ "./containers/Viewer.vue")
	},
	{
		path: "/users/register",
		name: 'users-register',
		component: () => import(/*webpackChunkName: "register" */ "./containers/users/Register.vue")
	},
	{
		path: "/users/login",
		name: 'users-login',
		component: () => import(/*webpackChunkName: "login" */ "./containers/users/Login.vue")
	},
	{
		path: "/users/profile",
		name: 'profile',
		component: () => import(/*webpackChunkName: "profile" */ "./containers/users/Profile.vue")
	},
	{
		path: "/admin/admin",
		name: 'admin',
		component: () => import(/*webpackChunkName: "admin" */ "./containers/admin/Admin.vue")
	},
	{
		path: '/search',
		name: 'search',
		component: () => import(/*webpackChunkName: "search" */ "./containers/Search.vue")
	},
	{
		path: "*",
		name: 'not-found',
		component: require("./containers/NotFound.vue")
	}
	],
	// eslint-disable-next-line no-unused-vars
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }; 
	},
});

new Vue({
	apolloProvider,
	router,
	store,
	render: h => h(App)
}).$mount("#app");