<template>	
	<router-link :to="{ path: '/serie/' + serie.id }" id="it-card-serie" class="uk-link-reset uk-text-center uk-position-relative">
		<img :data-src="serie.cover.url" :alt="serie.Title" class="uk-border-rounded" v-if="serie.cover" uk-img>
		<img :data-src="placeholder" :alt="serie.Title" class="uk-border-rounded" v-else uk-img />
		<p class="uk-card-badge uk-label uk-label-success it-label-new" v-if="showNewBadge(serie)" >{{'NEW' | translate}}</p>
		
		<!-- <p class="uk-card-badge uk-label it-label-finish" v-if="serie.is_finish">{{'COMPLETE' | translate}}</p>
		<p class="uk-card-badge uk-label it-label-finish" v-else>{{'ONGOING' | translate}}</p> -->
		
		<h3 class="uk-margin-small-top uk-margin-remove-bottom uk-text-truncate" :title="serie.Title">{{ serie.Title }}</h3>
		<p v-if="serie.genres" class="it-serie-genre">
			<span v-for="genre in serie.genres" :key="genre.id">
				{{ genre.name }}
			</span>
		</p>
	</router-link>
</template>

<script>
	var moment = require("moment");
	export default {
		name: "SerieCard",
		data: function() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				placeholder: require('@/assets/img/placeholder.png')
			};
		},
		methods:{
			showNewBadge(serie){
				var updatedDate = moment(serie.updatedAt);
				const diffDays = moment().diff(updatedDate, 'days');
				return diffDays <= 3;
			}
		},
		props: {
			serie: Object,
		},
		components: {
		},
	};
</script>