<template>
	<div class="uk-flex uk-flex-middle uk-flex-center uk-text-center">
		<vue-headful title="Redirigiendo - Ink&Tales" />
		<div>
			<h1 class="uk-margin-remove-bottom">Redirigiendo</h1>
			<h2 class="uk-margin-small-top">Por favor espera...</h2>
		</div>
	</div>
</template>

<script>
	import strapi from "../../utils/Strapi";
	export default {
		name: "Redirigiendo",
		mounted() {
			console.log(this.$route.query.planid);
			setTimeout(() => {
				var userId = this.$store.getters["auth/getId"];
				this.saveUserPlan(this.$route.query.planid,userId);
				this.$router.replace({ query: {} });
			}, 10);
		},
		methods: {
			async saveUserPlan(planId, userId) {
				await strapi.updateEntry("users", userId, {
					plan_id: planId,
				}).then(function(){
					location.replace(process.env.VUE_APP_URL + "/#/gracias")
					//location.replace("http://inkandtales.com/#/gracias")
				});
			}
		}
	};
</script>