<template>
	<div id="it-serie-list" class="uk-grid uk-grid-small uk-child-width-1-4@m uk-grid-small uk-grid-match" :class="classProp" uk-grid>
		<div v-for="serie in series" :key="serie.id">
			
			<router-link :to="{ path: '/serie/' + serie.id }" id="it-card-serie" class="uk-link-reset uk-width-1-1">
				<img :data-src="serie.cover.url" :alt="serie.Title" class="uk-border-rounded" v-if="serie.cover" uk-img>
				<img :data-src="placeholder" :alt="serie.Title" class="uk-border-rounded" v-else uk-img />
				<h3 class="uk-margin-small-top uk-margin-remove-bottom uk-text-truncate" :title="serie.Title">{{ serie.Title }}</h3>
				<p v-if="serie.genres" class="it-serie-genre">
					<span v-for="genre in serie.genres" :key="genre.id">
						{{ genre.name }}
					</span>
				</p>
			</router-link>

		</div>
	</div>
</template>

<script>
	import { ORIGINAL_LIST }  from '../constants/graphql';
	export default {
		name: "SeriesListOriginals",
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				placeholder: require('@/assets/img/placeholder.png'),
				series: [],
				id: null
			};
		},
		props: {
			classProp: String,
		},
		apollo: {
			series: {
				query: ORIGINAL_LIST,
				variables() {
					return { userId: this.id };
				},
				skip() {
					return this.id !=null;
				}
			}
		}
	};
</script>